<template>
  <div class="grade font_16 lineheight_3 padding_r_20">
    <div>
      <div id="myChart" style="height:400px;"></div>
      <Star :score="parseFloat(project.total_score)" :width="30" color="red"></Star>
      <div class="red font_bold text_center lineheight_2" style="font-size:36px;">{{ parseFloat(project.total_score).toFixed(1) }}</div>
    </div>
    <div class="padding_h_70 padding_b_70">
      <div class="">投资意见：<span class="red">{{ project.expert_advice }}</span></div>
      <!-- <div class="" v-html="'专家分析：'+project.rate_report"></div> -->
      <div v-html="project.rate_report"></div>
    </div>
    <!-- <div class="padding_h_70 padding_b_70">
      <div class="article_title">主体背景分析</div>
      <div v-for="b in bg">
        <div class="font_bold">{{ b.title }}</div>
        <div class="">{{ b.text }}</div>
      </div>
    </div>
    <div class="padding_h_70 padding_b_70">
      <div class="article_title">ICO计划</div>
      <div>
        <div class="" v-for="p in plan">{{ p }}</div>
      </div>
    </div> -->
  </div>
</template>

<script>
import api from '@/api'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/radar'
import Star from '@/components/Parts/Star'
export default {
  name: 'footer',
  data () {
    return {
      project: {total_score: 0},
      stuation: {advice: '2017年05月21日 — 2017年06月20日', analysis: '三大特性打造跨链数字资产基础设施平台，以去中心化的方式完成不同区块链网络的连接及价值的交换。由原Factom联合创始人及CTO吕旭军发起。'},
      bg: [{title: '刘强（白菜）', text: 'BitShares 社区深度参与者,HelloBTS创始人, YOYOW 项目创始人。'}, {title: '刘嘉陵（巨蟹）', text: '国内早期区块链项目布道者和投资者，比特创业营创始成员，比特股和以太坊项目早期参与者，比特股理事会理事。'}, {title: '刘嘉陵（巨蟹）', text: '国内早期区块链项目布道者和投资者，比特创业营创始成员，比特股和以太坊项目早期参与者，比特股理事会理事。'}],
      plan: ['- 基于石墨烯引擎，秒级的确认时间和强大的承载能力。', '- 扩展方便的插件开发和第三方接入架构', '- 终端用户无需下载区块链，即开即用。', '- 支持WEB、桌面应用、手机APP等多种客户端', '- 代币持有人投票决定重大事项', '- 高级多重签名，更方便、更灵活、更安全']
    }
  },
  created () {
    api.post('detail/project_score', {sign: 'token=0&project_id=1'}).then(res => {
      console.log(res)
      this.project = res
      this.drawLine()
    })
  },
  methods: {
    drawLine () {
      let myChart = echarts.init(document.getElementById('myChart'))
      myChart.setOption({
        title: {
          text: '基础雷达图'
        },
        tooltip: {},
        legend: {
          data: ['预算分配（Allocated Budget）', '实际开销（Actual Spending）']
        },
        radar: {
          name: {
            textStyle: {
              color: '#666',
              backgroundColor: 'transparent',
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          indicator: [
            {name: '项目团队', max: 10},
            {name: '技术能力', max: 10},
            {name: '安全保障', max: 10},
            {name: '商业逻辑', max: 10},
            {name: '发行方案', max: 10}
          ]
        },
        series: [{
          type: 'radar',
          data: [
            {
              value: [this.project.project_team_score, this.project.technology_score, this.project.safety_score, this.project.business_score, this.project.plan_score]
            }
          ],
          areaStyle: {
            normal: {
              color: '#c23531'
            }
          }
        }]
      })
    }
  },
  components: {
    Star
  }
}
</script>





